.category-list {
  padding: 1rem;
}

.category-list > div {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: space-evenly;
}

.category-list div div {
  display: flex;
  flex-direction: column;
}

.category-list img {
  width: 146px;
  margin: 0;
}

.category-list p {
  background-color: #000000;
  color: #ffffff;
  margin: 0;
  padding: 0.3rem;
}

@media screen and (min-width: 768px) {
  .category-list img {
    width: 209px;
  }
}

@media screen and (min-width: 1440px) {
  .category-list {
    padding: 1rem 10rem;
  }

  .category-list img {
    width: 160px;
  }
}
