.subscribe {
  padding: 2rem;
}

.subscribe > div {
  border: 1px solid #000000;
  padding: 1.5rem 1rem;
  margin: 0 auto;
}

.subscribe input {
  width: 60%;
  padding: 7px 15px;
}

.subscribe button {
  background-color: rgba(147, 83, 255, 1);
  color: #ffffff;
  padding: 9px 16px;
  border: none;
}

@media screen and (min-width: 768px) {
  .subscribe p {
    font-size: 20px;
  }
}

@media screen and (min-width: 1440px) {
  .subscribe {
    padding: 4rem;
  }

  .subscribe > div {
    width: 50%;
  }
}
