.product-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  margin-bottom: 2rem;
}

.product-list > h1 {
  font-size: 28px;
}

.product-list > div {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  justify-content: center;
}

.product-card {
  display: flex;
  flex-direction: column;
  width: 330px;
  align-items: flex-start;
  border: 1px solid rgba(0, 0, 0, 0.13);
  border-radius: 5px;
}

.product-card img {
  width: 100%;
}

.product-card div {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0 1rem 1rem;
}

.product-card div h2 {
  font-size: 16px;
  margin: 0.7rem 0 1rem;
}

.product-card div h2 + p {
  text-align: start;
  font-size: 13px;
  margin: 0 0 1rem;
}

.product-price {
  font-size: 16px;
  font-weight: 700;
  margin: 0 0 1rem;
}

.product-card button {
  background-color: rgba(147, 83, 255, 1);
  color: #ffffff;
  padding: 9px 16px;
  border: none;
}

@media screen and (min-width: 768px) {
  .product-list > div {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1.5rem;
  }
}

@media screen and (min-width: 1440px) {
  .product-list {
    padding: 1rem 10rem;
  }

  .product-list > h1 {
    font-size: 32px;
  }

  .product-card {
    width: 350px;
  }
}
