.subscribeModal {
  width: 343px;
  height: fit-content;
  background-color: #ffffff;
  margin: 5vh auto;
  overflow: auto;
  -webkit-box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.5);
  box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.5);
}

.subscribeModal-header {
  background-color: #000000;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.subscribeModal-header > div {
  display: flex;
  gap: 1rem;
}

.subscribeModal-header div > h2 {
  font-size: 20px;
  color: #ffffff;
  width: 211px;
  margin: 0;
}

.subscribeModal-body {
  padding: 0 1rem 1rem 1rem;
}

@media screen and (min-width: 768px) {
  .subscribeModal {
    width: 700px;
  }

  .subscribeModal-header div > h2 {
    width: fit-content;
  }
}
