.perks-section {
  display: flex;
  flex-direction: column;

  background-color: #000000;
  color: #ffffff;
  padding: 1.5rem;
}
.perks-section h2 {
  font-size: 1.5rem;
}

.perk {
  display: grid;
  grid-template-areas:
    "img title"
    "img description";
  column-gap: 1rem;
  align-items: center;
  text-align: start;
  width: 90%;
}

.perk img {
  grid-area: img;
}

.perk h3 {
  grid-area: title;
  text-transform: uppercase;
  color: rgba(218, 255, 1, 1);
  margin-bottom: 0;
}

.perk p {
  grid-area: description;
}

@media screen and (min-width: 768px) {
  .perks-section h2 {
    font-size: 32px;
  }

  .perks-section > div {
    margin: 0 auto;
  }
  .perk {
    margin: 0 auto;
    justify-content: flex-start;
    width: 75%;
  }
}

@media screen and (min-width: 1440px) {
  .perks-section > div {
    display: flex;
  }

  .perk {
    width: 25%;
  }
}
