.header .navbar {
  background-color: #000000;
  display: flex;
  flex-direction: column;
  padding: 0.5rem 1rem;
}

.navbar-links {
  display: flex;
  justify-content: space-between;
}

.navbar-links a {
  display: none;
}

.searchBar-768up {
  display: none;
}

.searchBar-Mobile {
  display: flex;
  gap: 0.2rem;
  justify-content: center;
  padding: 1.2rem;
}

.searchBar-Mobile > input {
  width: 60%;
  padding: 0.5rem 1rem;
}

.searchBar-Mobile > button {
  background-color: #ffffff;
}

.navbar-menu-inactive {
  display: none;
}

.navbar-menu-active {
  display: flex;
  z-index: 1000;
  top: 0;
  right: 0;
  flex-direction: column;
  width: fit-content;
  padding: 1rem 1rem 0;
  position: absolute;
  background-color: #343a40;
}

#menu img {
  width: 16px;
  align-self: flex-end;
}

#menu ul {
  padding: 0;
  margin-top: 0;
  margin-bottom: 1rem;
}

#menu li {
  list-style: none;
  text-align: left;
}

#menu a {
  display: block;
  width: 100%;
  text-decoration: none;
  color: white;
  border-bottom: 1px solid #ffffff;
  padding: 0.5rem 0 0.5rem 0;
}

#menu li:last-child a {
  border: none;
}

@media screen and (min-width: 768px) {
  .header .navbar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .navbar-links {
    display: flex;
    justify-content: space-between;
    gap: 1rem;
    align-items: center;
  }
  .navbar-links a {
    display: block;
    color: #ffffff;
    text-decoration: none;
    font-size: 13px;
  }

  .menu-button {
    display: none;
  }

  #menu {
    display: none;
  }

  .searchBar-Mobile {
    display: none;
  }

  .searchBar-768up {
    display: flex;
    gap: 1rem;
  }

  .searchBar-768up > input {
    width: 145px;
    padding: 0.5rem 1rem;
  }

  .searchBar-768up > button {
    background-color: #000000;
    border: 1px outset #ffffff;
    color: #ffffff;
  }
}

@media screen and (min-width: 1440px) {
  .navbar-links {
    gap: 2rem;
  }

  .navbar-links a {
    font-size: 16px;
  }
}
