.productModal {
  width: 343px;
  height: 90vh;
  background-color: #ffffff;
  margin: 5vh auto;
  overflow: auto;
  -webkit-box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.5);
  box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.5);
}
.productModal-header {
  background-color: #000000;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.productModal-header > div {
  display: flex;
  gap: 1rem;
}

.productModal-header div > h2 {
  font-size: 20px;
  color: #ffffff;
  width: 211px;
  margin: 0;
}

.productModal-header + div {
  padding: 1rem;
}

.productModal-body {
  width: 311px;
  border: 1px solid rgba(0, 0, 0, 0.13);
  border-radius: 5px;
}

.productModal-body > img {
  width: 311px;
}

.productModal-item {
  padding: 0 1rem;
}

.productModal-item > h2 {
  font-size: 16px;
  font-weight: 700;
}

.productModal-item h2 + p {
  font-size: 13px;
  padding-bottom: 1rem;
  border-bottom: 1px solid #000000;
}

#productModal-item-price {
  font-weight: 500;
  font-size: 20px;
}

#productModal-item-price + p {
  color: rgba(173, 181, 189, 1);
  font-size: 13px;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(173, 181, 189, 1);
}

.selectorForms {
  padding: 0 1rem;
}

.selectorForms > p {
  font-size: 13px;
  font-weight: 700;
}

.selectorForms > form {
  display: flex;
  gap: 1rem;
  padding-bottom: 1rem;
}

.selectorForms > form > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.3rem;
}

.selectorForms > form > div > input {
  appearance: none;
  outline: none;
  border: 1px solid rgba(173, 181, 189, 1);
  border-radius: 16px;
  width: 13px;
  height: 13px;
  transition: 0.5s;
}

#azul_claro {
  background-color: rgba(117, 215, 240, 1);
}

#offwhite {
  background-color: rgba(243, 246, 247, 1);
}

#preto {
  background-color: #000000;
}

.selectorForms > form > div > input:checked {
  box-shadow: 0 0 1px 2px rgba(147, 83, 255, 1) inset;
  border: 1px solid rgba(147, 83, 255, 1);
}

.selectorForms > form > div > label {
  font-size: 13px;
}

.productModal-item-info + button {
  background-color: rgba(147, 83, 255, 1);
  border: none;
  padding: 9px 16px;
  margin-left: 1rem;
  margin-bottom: 1rem;
  font-weight: 500;
  color: white;
}

.productModal-item + .selectorForms > form {
  border-bottom: 1px solid rgba(173, 181, 189, 1);
}

@media screen and (min-width: 768px) {
  .productModal {
    width: 700px;
    height: fit-content;
  }

  .productModal-header div > h2 {
    width: fit-content;
  }

  .productModal-body {
    width: fit-content;
    display: grid;
    grid-template-areas:
      "image product"
      "image selectors"
      "blank button";
    grid-template-columns: auto 1fr;
    justify-items: start;
    border: none;
  }

  .productModal-body > img {
    grid-area: image;
  }

  .modal-product-info {
    grid-area: selectors;
  }
  .productModal-item-info + button {
    grid-area: button;
    margin: 1rem;
  }

  .selectorForms > form {
    padding-bottom: 0;
  }
}
